.product{
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-end;
  margin: 10px;
  padding: 20px;
  width: 100%;
  max-height: 400px;
  min-width: 100px;
  /* background-color: rgb(86, 128, 130); */
  /* background: linear-gradient(180deg, rgb(0, 0, 9) 0%, rgb(2, 28, 53) 32%, rgb(3, 31, 4) 78%, rgb(14, 35, 39) 100%); */
  z-index: 10;
  transition: transform .2s ease-in,
  opacity .4s, box-shadow .4s;
  /* box-shadow: 0px 0px 22px 6px rgba(0,0,0,0.75); */
  opacity: .9;
  color: #fff;
}

.product:hover{
  opacity: 1;
  /* background: linear-gradient(180deg, rgb(5, 5, 70) 0%, rgb(10, 43, 73) 32%, rgb(23, 79, 25) 78%, rgb(18, 99, 116) 100%); */
  background-color: rgba(255, 255, 255, 0.131);
  box-shadow: 0px 0px 22px 6px rgba(0,0,0,0.75);
}

.product > img {
  max-height: 200px;
  width: 100%;
  object-fit: contain;
  margin-bottom: 15px;
}

.product > button {
  background: #90e1df;
  border: 1px solid;
  border-color: #000 1px;
  color: #111;
  box-shadow: 0px 0px 22px 6px rgba(0,0,0,0.75);
}

.product__price{
  margin-top: 5px;
}

.product__info{
  height: 100px;
  margin-bottom: 15px;
}

.product__rating{
  margin-bottom: 20px;

}

.product:hover{
  transform: scale(1.03);
  cursor: pointer;
}

@media (max-width: 600px) {
  .product{
    margin: 20px 0;
    
  }

  .product__info{
    height: fit-content;
    display: flex;
    flex-direction: column;
  }
}