.subtotal {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  width: 300px;
  height: 100px;
  padding: 20px;
  background-color: #f3f3f3;
  border: 1px solid #dddddd;
  border-radius: 3px;
}

.subtotal__gift {
  display: flex;
  align-items: center;
}

.subtotal__gift > input {
  margin-right: 5px;
}

.subtotal > button {
  background: #9fcfe6;
  border-radius: 2px;
  width: 100%;
  height: 30px;
  margin-top: 10px;
  border-color:black 2px solid;
  color: #111;
}


@media (max-width:600px){

  .subtotal{
    width: 90vw;
  }

}