.login {
  
  height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.login__logo {
  margin-top: 20px;
  margin-bottom: 20px;
  object-fit: contain;
  width: 100px;
  margin-right: auto;
  margin-left: auto;
}

.login__container {
  background-color: white;
  width: 300px;
  height: fit-content;
  display: flex;
  flex-direction: column;
  border-radius: 5px;
  border: 1px solid lightgray;
  padding: 20px;
}


.login__container > h1 {
  font-weight: 500;
  margin-bottom: 20px;
}

.login__container > form > h5 {
  margin-bottom: 5px;
}

.login__container > form > input {
  height: 30px;
  margin-bottom: 10px;
  background-color: #fff;
  width: 98%;
}

.login__container > p{
  margin-top: 15px;
  font-size: 12px;
}

.login__signInButton{
  background: #4bf0ed;
  border: 1px solid;
  margin-top: 10px;
  border-color: #000 1px solid;
  color: #111;
  width: 100%;
  padding: 10px;
}

.login__registerButton{
  background: #417b96;
  border: 1px solid;
  margin-top: 10px;
  color: #fff;
  padding: 10px;
  border: 2px #333 solid;
}