.checkoutProduct{
  display: flex;
  margin-top: 20px;
  margin-bottom: 20px;
  /* border: 2px dotted rgb(190, 244, 248); */
  padding: 7px;
  box-sizing: border-box;
}

.checkoutProduct__info{
  padding-left: 20px;

}

.checkoutProduct__info > button {
  background: #ff0000;
  border: 1px solid #000;
  margin-top: 10px;
  /* border-color: #000 solid px; */
  color: #fff;
}

.checkoutProduct__image{
  object-fit: contain;
  width: 180px;
  height: 180px;
}

.checkoutProduct__rating {
  display: flex;
}

.checkoutProduct__title {
  font-size: 17px;
  font-weight: 800;
}