@media (max-width:600px){
  .slider{
    width: 100vw;
  }
}


.slider{
  position: relative;
  width: 100%;
  height: 100%;
}

.carousel_img{
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
}

.larr{
  position: absolute;
  top: 50%;
  font-size: 4rem;
  left: 10px;
  color: #fff;
}
.rarr{
  position: absolute;
  top: 50%;
  font-size: 4rem;
  right: 10px;
  color: #fff;
}

.shrink{
  animation: shrink .24s forwards;
}

@keyframes shrink {
  from{
    width: 100%;
  }
  to{
    width: 0%;
  }
}

.grow{
  animation: grow .24s forwards;
}

@keyframes grow {
  from{
    width: 0%;
  }
  to{
    width: 100%;
  }
}

.larr,
.rarr{
  cursor: pointer;
  width: 70px !important;
  height: 70px !important;
}
